import React, { useState } from 'react';
import Modal from 'react-modal';
import './styles.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        minWidth: '300px',
        maxWidth: '500px',
        padding: '25px',
        borderRadius: '8px',
        boxShadow: '0 5px 15px rgba(0,0,0,0.2)'
    },
    overlay: {
        backgroundColor: 'rgba(0, 0, 0, 0.75)',
        zIndex: 1000
    },
};

function LoginModal({ isOpen, onRequestClose, onLoginSubmit }) {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    const handleSubmit = async (event) => {
        event.preventDefault();
        setError('');

        if (!username || !password) {
            setError('Будь ласка, заповніть ім\'я користувача та пароль.');
            return;
        }

        const success = await onLoginSubmit({ username, password });

        if (success) {
            setUsername('');
            setPassword('');
            onRequestClose(); 
        } else {
        }
    };

    const handleClose = () => {
        setError('');
        setUsername('');
        setPassword('');
        onRequestClose();
    }

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={handleClose}
            style={customStyles}
            contentLabel="Форма Входу"
        >
            <h2>Вхід</h2>
            <form onSubmit={handleSubmit} className="modal-form">
                {error && <p className="error-message">{error}</p>}
                <div className="form-group">
                    <label htmlFor="login-username">Ім'я користувача:</label>
                    <input
                        type="text"
                        id="login-username"
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="login-password">Пароль:</label>
                    <input
                        type="password"
                        id="login-password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />
                </div>
                <div className="modal-actions">
                    <button type="submit" className="submit-btn">Увійти</button>
                    <button type="button" onClick={handleClose} className="cancel-btn">Скасувати</button>
                </div>
            </form>
        </Modal>
    );
}

export default LoginModal;