import React, { useState } from 'react';

function StarRating({ totalStars = 5, initialRating = 0, onRate, readonly = false, showRatingValue = true }) {
    const [hoverRating, setHoverRating] = useState(0);
    const [currentRating, setCurrentRating] = useState(initialRating);

    const handleMouseOver = (rating) => {
        if (!readonly) {
            setHoverRating(rating);
        }
    };

    const handleMouseLeave = () => {
        if (!readonly) {
            setHoverRating(0);
        }
    };

    const handleClick = (rating) => {
        if (!readonly) {
            setCurrentRating(rating);
            if (onRate) {
                onRate(rating);
            }
        }
    };
    
    const displayRating = hoverRating || currentRating;

    return (
        <div className="star-rating-container">
            {[...Array(totalStars)].map((_, index) => {
                const starValue = index + 1;
                return (
                    <span
                        key={starValue}
                        className={`star ${starValue <= displayRating ? 'filled' : ''} ${readonly ? 'readonly' : ''}`}
                        onMouseOver={() => handleMouseOver(starValue)}
                        onMouseLeave={handleMouseLeave}
                        onClick={() => handleClick(starValue)}
                    >
                        ★
                    </span>
                );
            })}
            {showRatingValue && <span className="rating-value">({displayRating > 0 ? displayRating : initialRating}/ {totalStars})</span>}
        </div>
    );
}

export default StarRating;