import React, { useState } from 'react';
import Modal from 'react-modal';
import './styles.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        minWidth: '300px',
        maxWidth: '500px',
        padding: '25px',
        borderRadius: '8px',
        boxShadow: '0 5px 15px rgba(0,0,0,0.2)'
    },
    overlay: {
        backgroundColor: 'rgba(0, 0, 0, 0.75)',
        zIndex: 1000 
    },
};

function RegistrationModal({ isOpen, onRequestClose, onRegisterSubmit }) {
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    const handleSubmit = async (event) => {
        event.preventDefault();
        setError(''); 

        if (!username || !email || !password) {
            setError('Будь ласка, заповніть усі поля.');
            return;
        }
        if (!/\S+@\S+\.\S+/.test(email)) {
            setError('Будь ласка, введіть дійсну адресу email.');
            return;
        }
        const success = await onRegisterSubmit({ username, email, password });

        if (success) {
            setUsername('');
            setEmail('');
            setPassword('');
            onRequestClose();
        } else {
            setError('Помилка реєстрації. Можливо, користувач вже існує.');
        }
    };

    const handleClose = () => {
        setError('');
        setUsername('');
        setEmail('');
        setPassword('');
        onRequestClose();
    }

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={handleClose}
            style={customStyles}
            contentLabel="Форма Реєстрації"
        >
            <h2>Реєстрація</h2>
            <form onSubmit={handleSubmit} className="modal-form">
                {error && <p className="error-message">{error}</p>}
                <div className="form-group">
                    <label htmlFor="reg-username">Ім'я користувача:</label>
                    <input
                        type="text"
                        id="reg-username"
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="reg-email">Email:</label>
                    <input
                        type="email" 
                        id="reg-email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="reg-password">Пароль:</label>
                    <input
                        type="password" 
                        id="reg-password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />
                </div>
                <div className="modal-actions">
                    <button type="submit" className="submit-btn">Зареєструватись</button>
                    <button type="button" onClick={handleClose} className="cancel-btn">Скасувати</button>
                </div>
            </form>
        </Modal>
    );
}

export default RegistrationModal;
